
// src/components/ArticleList.js
import React from 'react';
import { Link } from 'react-router-dom';

const ArticleList = () => {
    return (
        <section className="blog-content">
            <article className="post">
                <h2></h2>
                <p className="post-meta">2025217 | </p>
                <p>...</p>
                <Link to="/articles/1" className="read-more"></Link>
            </article>

            <article className="post">
                <h2></h2>
                <p className="post-meta">2025216 | </p>
                <p>...</p>
                <Link to="/articles/2" className="read-more"></Link>
            </article>
        </section>
    );
};

export default ArticleList;
