// src/components/Header.js
import React from 'react';
import { Link } from 'react-router-dom';

const Header = () => {
    return (
        <header>
            <nav>
                <ul>
                    <li><Link to="/"></Link></li>
                    <li><Link to="/articles"></Link></li>
                    <li><Link to="/about"></Link></li>
                    <li><Link to="/contact"></Link></li>
                </ul>
            </nav>
        </header>
    );
};

export default Header;
